/* 
 * Whitespace Reset */
* {
  padding: 0;
  margin: 0; }

/* 
 * Box-sizing fix */
html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

/* 
 * BrowserSync Notification */
#__bs_notify__ {
  display: none !important; }

@keyframes navtogglein {
  from {
    opacity: 0;
    transform: translate(0, -2rem); }
  to {
    opacity: 1;
    transform: translate(0, 0); } }

@keyframes jump {
  0% {
    transform: translate(0, 0); }
  50% {
    transform: translate(0, -0.3rem); }
  100% {
    transform: translate(0, 0); } }

@keyframes navitemin {
  from { }
  to {
    opacity: 1;
    transform: scale(1) translate(0, 0) rotate(0deg); } }

@keyframes shake {
  0% {
    transform: translate(-2rem, 0); }
  25% {
    transform: translate(2rem, 0); }
  50% {
    transform: translate(-2rem, 0); }
  75% {
    transform: translate(2rem, 0); }
  100% {
    transform: translate(0, 0); } }

@keyframes full-circle {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(-360deg); } }

@keyframes criss-cross-right {
  0% {
    right: -20px; }
  50% {
    right: 50%;
    width: 20px;
    height: 20px; }
  100% {
    right: 50%;
    width: 375px;
    height: 375px; } }

@-webkit-keyframes criss-cross-right {
  0% {
    right: -20px; }
  50% {
    right: 50%;
    width: 20px;
    height: 20px; }
  100% {
    right: 50%;
    width: 375px;
    height: 375px; } }

@keyframes criss-cross-left {
  0% {
    left: -20px; }
  50% {
    left: 50%;
    width: 20px;
    height: 20px; }
  100% {
    left: 50%;
    width: 375px;
    height: 375px; } }

@-webkit-keyframes criss-cross-left {
  0% {
    left: -20px; }
  50% {
    left: 50%;
    width: 20px;
    height: 20px; }
  100% {
    left: 50%;
    width: 375px;
    height: 375px; } }

