  transform: translateZ(0);
  transition: transform 750ms cubic-bezier(0.23, 1, 0.32, 1); }
  .item-image::before {
    content: "";
    display: block;
    padding-top: 75%;
    overflow: hidden; }
  .item-image img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    line-height: 0; }

.item-text {
